import Table from "react-bootstrap/Table";
import List from "./List";

function ListView({ tickers, search }) {
    return (
        <Table responsive>
            <thead>
                <tr>
                    <th> Rank </th>
                    <th> Name </th>
                    <th> Price </th>
                    <th className="d-none d-md-table-cell"> Low </th>
                    <th className="d-none d-md-table-cell"> High </th>
                    <th className="d-none d-sm-table-cell"> Change (1h) </th>
                    <th className="d-none d-lg-table-cell"> Change (24h) </th>
                    <th className="d-none d-lg-table-cell"> Change (7d) </th>
                </tr>
            </thead>
            <tbody>
                {tickers.filter((ticker)  => {return ticker.id.toLowerCase().includes(search.toLowerCase());})
                .map(
                    (ticker, indx) => (
                    <List
                        key={ticker.symbol}
                        rank={ticker.market_cap_rank}
                        indx={indx}
                        name={ticker.id}
                        symb={ticker.symbol}
                        price={+ticker.current_price}
                        low={+ticker.low_24h}
                        high={+ticker.high_24h}
                        change1h={+ticker.price_change_percentage_1h_in_currency}
                        change24h={+ticker.price_change_percentage_24h_in_currency}
                        change7d={+ticker.price_change_percentage_7d_in_currency}
                    />
                ))}
            </tbody>
        </Table>
    );
}

export default ListView;
