import Header from "./components/Header";
import AddTicker from "./components/AddTicker";
import Footer from "./components/Footer";
import Container from "react-bootstrap/Container";
import { useState, useEffect } from "react";
import { axios } from "axios";
import ListView from "./components/ListView";


function App() {
    let id = 0;

    const [tickers, setTickers] = useState(
        [
            {
                id: "bitcoin",
                name: "Bitcoin",
                symbol: "BTC",
                price_usd: "1",
                percent_change_1h: "0",
                percent_change_24h: "0",
                percent_change_7d: "0",
            },
        ]
    );

    const [search, setSearch] = useState("");

    const [favTickers, setFavTickers] = useState(
        (JSON.parse(localStorage.getItem("data")) !== null && JSON.parse(localStorage.getItem("data")) !== undefined)
            ? JSON.parse(localStorage.getItem("data"))
            : ["etc", "btc", "ltc", "usdt", "ada", "bnb", "xrp", "sol", "usd", "dot"]
    );

    function addTicker(symbol) {
        setFavTickers(favTickers.concat(symbol.ticker.toLowerCase()));
    }

    function deleteTicker(symbol) {
        if (tickers.length > 1) {
            setFavTickers(favTickers.filter((ticker) => ticker !== symbol));
        }
    }

    function fetchData() {
        axios.get("https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=50&page=1&sparkline=false&price_change_percentage=1h%2C24h%2C7d")
            .then(response => {
                //const result = response.data.filter(ticker => favTickers.includes(ticker.symbol));
                setTickers(response.data);
            })
            .catch(err => console.log(err));
    }

    useEffect(() => {
        fetchData();
        localStorage.setItem("data", JSON.stringify(favTickers));
    });


    const title = "Crypto - the Future Currency";

    return (
        <>
            <Header onSearch={setSearch} title={title} />
            <Container className="align-content-center pt-5">
                {tickers.length > 0 ? (
                    <ListView onDelete={deleteTicker} id={id} tickers={tickers} search={search}/>
                ) : (
                    'No crypto to show'
                )}
            </Container>
            <Footer />
        </>

    );
}

export default App;
