import { GrFavorite } from "react-icons/gr";

function List({ rank, name, symb, price, low, high, change1h, change24h, change7d, indx }) {
    const fav = true;
    return (
        <tr>
            <td> <GrFavorite onClick className={`bg-${(fav)?"dark":"light"}`} /> {indx} </td>
            <td> {name} <div className="lead d-inline ps-2"> {symb.toUpperCase()} </div> </td>
            <td className="text-end"> {price} </td>
            <td className="d-none text-end d-md-table-cell">{low.toFixed(2)}</td>
            <td className="d-none text-end d-md-table-cell">{high.toFixed(2)}</td>
            <td className={`text-end text-${(change1h>=0) ? "success" : "danger"} d-none d-sm-table-cell`}>{change1h.toFixed(2)}%</td>
            <td className={`text-end text-${(change1h>=0) ? "success" : "danger"} d-none d-md-table-cell`}>{change24h.toFixed(2)}%</td>
            <td className={`text-end text-${(change1h>=0) ? "success" : "danger"} d-none d-md-table-cell`}>{change7d.toFixed(2)}%</td>
        </tr>
    );
}

export default List;
