function Header({ title, onSearch }) {
    return (
        <>
            <header className="header bg-dark text-light p-4 mb-4">
                <h2 className="display-1 text-center"> {title} </h2>
            </header>
            <input className="float-end me-3 me-sm-4 me-md-5"
                type="text"
                placeholder="search"
                onChange={(e) => {
                    onSearch(e.target.value);
                }}
            />
        </>
    );
}

export default Header
